import { css } from '../styled';
import { color, fontFamily, transitionTime } from '../theme';

export const withLinkStyle = css`
	cursor: pointer;
	font-size: inherit;
	font-weight: inherit;
	line-height: inherit;
	color: ${color('interactive.primary.idle')};
	transition: color ${transitionTime()};
	background-color: transparent;
	padding: 0;
	border: none;
	font-family: ${fontFamily('default')};

	&:hover,
	&:focus {
		color: ${color('interactive.primary.hover')};
	}
	&:active {
		color: ${color('interactive.primary.pressed')};
	}
	&:active:hover {
		color: ${color('interactive.primary.pressedHover')};
	}
`;
